.header-main-container {
  height: 10%;
  width: 100%;
  margin-top: 0px;
  padding: 1px 0px;
  border-bottom: 1px solid #858aa4;
  display: flex;
  align-items: center;
  justify-content: space-between;
  background-color: #fff;
}

.type-heading {
  color: #343c6a;
  font-family: "Bree Serif";
  font-size: 28px;
  font-style: normal;
  font-weight: 500;
  line-height: normal;
  margin-left: 30px;
}

.add-transaction-button {
  border-radius: 12px;
  background: #2d60ff;
  display: flex;
  padding: 8px 12px;
  justify-content: center;
  align-items: center;
  margin-right: 30px;
  color: white;
  border: none;
  outline: none;
  font-weight: 500;
  cursor: pointer;
}

.popup-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.9);
  z-index: 9999;
  display: flex;
  justify-content: center;
  align-items: center;
}

.transaction-popup-container {
  display: flex;
  align-items: flex-start;
  flex-direction: column;
  width: 27%;
  height: 66%;
  border-radius: 25px;
  background-color: #fff;
  padding: 14px;
  padding-left: 20px;
}

.heading-container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  margin-bottom: 5px;
}

.f1 {
  color: #333b69;
  font-family: "Roboto";
  font-size: 20px;
  font-style: normal;
  font-weight: 700;
}

.f2 {
  color: #505887;
  font-family: "Roboto";
  font-size: 14px;
  font-style: normal;
  font-weight: 500;
  margin-top: 0px;
}

.f3 {
  color: #4e536e;
  font-family: Cambria, Cochin, Georgia, Times, "Times New Roman", serif;
  font-size: 18px;
  font-style: normal;
  font-weight: 500;
  line-height: normal;
}

.f4 {
  border-radius: 10px;
  border: 2px solid #c6cdd2;
  background-color: #fff;
  height: 40px;
  width: 90%;
  margin-bottom: 12px;
  outline: none;
  padding-left: 10px;
}

.f5 {
  border-radius: 10px;
  border: 2px solid #c6cdd2;
  background-color: #fff;
  height: 43px;
  width: 93%;
  margin-bottom: 12px;
  outline: none;
  padding-left: 10px;
}

.f6 {
  width: 96%;
  border-radius: 8px;
  background: var(--primary-3, #2d60ff);
  display: flex;
  padding: 8px 16px;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  outline: none;
  border: none;
  color: #fff;
  font-weight: 600;
  margin-top: 10px;
}
