.sidebar-main-container {
  width: 18%;
  height: 100%;
  border-right: 1px solid #858aa4;
}

.sidebar-logo-container {
  width: 100%;
  height: 7%;
  display: flex;
  align-items: center;
  padding: 16px 10px 10px 0px;
  justify-content: center;
}

.sidebar-money {
  color: #f89a23;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 22px;
  font-weight: 800;
  line-height: normal;
  margin-left: 10px;
}

.sidebar-matters {
  color: #02969c;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 22px;
  font-weight: 800;
  line-height: normal;
}

.list-container {
  height: 74%;
  display: flex;
  flex-direction: column;
  padding-left: 0px;
}

.list-item {
  display: flex;
  align-items: center;
  list-style: none;
  cursor: pointer;
  margin-top: 6%;
  padding-left: 30px;
}

.item-name {
  color: #505887;
  font-family: "Roboto";
  font-size: 20px;
  font-style: normal;
  font-weight: 600;
  line-height: normal;
  margin-left: 10px;
}

.active-item-name {
  color: #2d60ff;
  font-family: "Roboto";
  font-size: 20px;
  font-style: normal;
  font-weight: 600;
  line-height: normal;
  margin-left: 10px;
}

.profile-container {
  height: 10%;
  display: flex;
  align-items: center;
  padding: 0px 15px;
  justify-content: space-between;
  margin-top: auto;
}

.profile-img {
  width: 20%;
  margin-top: auto;
  margin-bottom: 12px;
}

.user-name {
  color: #505887;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 14px;
  font-style: normal;
  font-weight: 600;
  line-height: 20px;
  margin-right: 5px;
}

.user-email {
  color: #718ebf;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 20px;
  margin-top: 0px;
}

.name-logout-container {
  display: flex;
  align-items: center;
  width: 100%;
  justify-content: space-between;
  margin-bottom: 0px;
}

.details-container {
  margin-top: auto;
  width: 70%;
  margin-bottom: 0px;
}

.logout-button {
  border: none;
  outline: none;
  cursor: pointer;
  background-color: transparent;
}

.active {
  border-left: 5px solid blue;
}

.link {
  text-decoration: none;
}

.popup-container {
  display: flex;
  align-items: center;
  width: 35%;
  height: 25%;
  border-radius: 25px;
  background-color: #fff;
}

.icon-logout-container {
  width: 20%;
  display: flex;
  justify-content: center;
}

.popup-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.9);
  z-index: 9999;
  display: flex;
  justify-content: center;
  align-items: center;
}

.are {
  color: #333b69;
  font-family: "Roboto";
  font-size: 20px;
  font-style: normal;
  font-weight: 700;
  line-height: 28px;
}

.lorem {
  color: #505887;
  font-family: "Roboto";
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 20px; /* 142.857% */
}

.logout {
  border-radius: 8px;
  background-color: #dc2626;
  display: flex;
  padding: 8px 16px;
  justify-content: center;
  align-items: center;
  color: #fff;
  border: none;
  cursor: pointer;
  font-weight: 500;
}

.cancel {
  border-radius: 8px;
  border: 1px solid #cbd5e1;
  background-color: #fff;
  color: #000;
  display: flex;
  padding: 8px 16px;
  justify-content: center;
  align-items: center;
  gap: 10px;
  margin-left: 30px;
  cursor: pointer;
}

.logout-popup-buttons {
  display: flex;
  align-items: center;
}
