.seven-days-container {
  width: 92%;
  border-radius: 20px;
  margin-left: 40px;
  margin-top: 20px;
  padding: 10px;
  background-color: #fff;
  margin-bottom: 20px;
}

.last-seven-days-details {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.days-details1 {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 12%;
  margin-right: 20px;
}

.head {
  color: #718ebf;
  font-family: "Roboto";
  font-size: 16px;
  font-style: normal;
  font-weight: 600;
  line-height: normal;
  margin-left: 5px;
}

.details1 {
  display: flex;
  align-items: center;
}

.heading1 {
  color: #718ebf;
  font-family: "Roboto";
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  margin: 10px 20px 40px 20px;
}

.span {
  color: #718ebf;
  font-family: "Roboto";
  font-size: 16px;
  font-style: normal;
  font-weight: 600;
  line-height: normal;
}

.credit-circle {
  border-radius: 5px;
  background: #fcaa0b;
  box-shadow: 0px 0px 15px 0px rgba(172, 13, 108, 0.5) inset;
  width: 15px;
  height: 15px;
  flex-shrink: 0;
}

.debit-circle {
  width: 15px;
  height: 15px;
  flex-shrink: 0;
  border-radius: 5px;
  background: #4c78ff;
  box-shadow: 0px 0px 15px 0px rgba(17, 52, 133, 0.5) inset;
}
