.transaction-header-main-container {
  height: 15%;
  width: 100%;
  margin-top: 0px;
  padding: 1px 0px;
  border-bottom: 1px solid #858aa4;
  background-color: #fff;
}

.type {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
}

.type2 {
  width: 20%;
  display: flex;
  align-items: center;
  list-style: none;
  justify-content: space-between;
}

.name6 {
  color: #718ebf;
  text-align: center;
  font-family: "Roboto";
  font-size: 16px;
  font-weight: 500;
  cursor: pointer;
}

.name5 {
  color: #2d60ff;
  text-align: center;
  font-family: "Roboto";
  font-size: 16px;
  font-weight: 500;
  padding-bottom: 7px;
  border-bottom: 2px solid #2d60ff;
  cursor: pointer;
}
